@font-face{
    font-family: 'Super-Smash-4';
    src: local('Super-Smash-4'), url('Smash4.woff') format('woff');
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Super-Smash-4';
  background: -webkit-gradient(linear, left top, right top, from(rgba(60,0,0,1)) , to(rgba(251,4,29,1)));
  background: -webkit-linear-gradient(left, rgba(60,0,0,1) 0% , rgba(251,4,29,1) 100%);
  background: -o-linear-gradient(left, rgba(60,0,0,1) 0% , rgba(251,4,29,1) 100%);
  background: linear-gradient(to right, rgba(60,0,0,1) 0% , rgba(251,4,29,1) 100%);
}



